@import './variables.scss';

* {
  font-family: 'Montserrat', sans-serif;
  font-display: swap;
  color: $text-main;
  box-sizing: border-box;
  scrollbar-width: auto;
  scrollbar-color: #e6007e #071b2c;
  &::-webkit-scrollbar {
    width: 4px;
    height: 4px;
  }
  &::-webkit-scrollbar-track {
    background: #071b2c;
  }
  &::-webkit-scrollbar-thumb {
    background-color: #e6007e;
    border-radius: 10px;
    border: 0px solid #071b2c;
  }
  &::-webkit-scrollbar-corner {
    display: none;
  }
}

* body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: $background-main;
}
button {
  cursor: pointer;
}
a {
  text-decoration: none;
}
.wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;

  .mainContainer {
    width: 100%;
    height: 100%;
    .bannerWrapper {
      width: 100%;
      height: 300px;
      @media (max-width: 450px) {
        height: 200px;
      }
      .menuBanner {
        width: 100%;
        height: 300px;
        object-fit: cover;
        @media (max-width: 450px) {
          height: 200px;
        }
      }
    }

    .menuWrapper {
      position: relative;
      margin: auto;
      max-width: 1440px;
      padding: 0 20px;
      height: 100%;
      display: grid;
      grid-template-columns: 1fr 3fr 1fr;
      @media (max-width: 820px) {
        grid-template-columns: 1fr 3fr;
      }
      @media (max-width: 490px) {
        grid-template-columns: 1fr;
        padding: 0;
      }
    }
  }
}
